// @ is an alias to /src
import NavBar from '@/components/NavBar'
import AsideMenu from '@/components/AsideMenu'
import FooterBar from '@/components/FooterBar'
import { mapState } from 'vuex'

export default {
  name: 'Home',
  components: {
    FooterBar,
    AsideMenu,
    NavBar
  },
  computed: {
    menu () {
      return [
        'General',
        [
          {
            to: '/',
            icon: 'home',
            label: 'Inicio'
          },
          {
            to: '/production',
            label: 'Módulo de producción',
            icon: 'cogs'
          },
          {
            to: '/forms',
            label: 'Datos de entrada',
            icon: 'square-edit-outline'
          },
          {
            to: '/log-curves',
            icon: 'trending-up',
            label: 'Base de datos'
          }
        ],
        'Gráficas',
        [
          {
            to: '/charts/3',
            label: '3D interactivos',
            icon: 'rotate-3d'
          },
          {
            to: '/charts/1',
            label: 'CILD vs PhiN',
            icon: 'chart-scatter-plot'
          },
          {
            to: '/charts/2',
            label: 'RHOB vs Neutron',
            icon: 'chart-scatter-plot'
          }
        ],
        'Modelos geomecánicos',
        [
          {
            to: '/charts',
            label: 'Modelos 3D interactivos',
            icon: 'rotate-3d'
          },
          {
            to: '/parametros-mecanicos',
            label: 'Parámetros',
            icon: 'chart-areaspline'
          },
          {
            to: '/mecanics/6',
            label: 'Modelo estructural',
            icon: 'chart-areaspline'
          },
          {
            to: '/mecanics/7',
            label: 'Mecánica estratigráfica',
            icon: 'chart-areaspline'
          },
          {
            to: '/mecanics/8',
            label: 'Presión de poro',
            icon: 'chart-areaspline'
          },
          {
            to: '/propiedades-mecanicas',
            label: 'Propiedades mecánicas',
            icon: 'chart-timeline'
          },
          {
            to: '/estabilidad',
            label: 'Análisis de estabilidad',
            icon: 'chart-scatter-plot-hexbin'
          },
          {
            to: '/geopresiones-finales',
            label: 'Geopresiones finales',
            icon: 'chart-line-stacked'
          }
        ]
      ]
    },
    menuBottom () {
      return [
        {
          action: 'logout',
          icon: 'logout',
          label: 'Log out',
          state: 'info'
        }
      ]
    },
    ...mapState(['isOverlayVisible'])
  },
  created () {
    this.$store.commit('user', {
      name: 'John Doe',
      email: 'john@example.com',
      avatar: 'https://avatars.dicebear.com/api/human/jjjjjjjjjjjj.svg'
    })
  },
  methods: {
    menuClick (item) {
      if (item.menuSecondary) {
        this.menuSecondary = item.menuSecondary
        this.menuSecondaryLabel = item.menuSecondaryLabel
          ? item.menuSecondaryLabel
          : null
        this.menuSecondaryIcon = item.menuSecondaryIcon
          ? item.menuSecondaryIcon
          : null

        this.$store.commit('asideActiveForcedKeyToggle', item)
        this.$store.commit('overlayToggle', true)
      } else if (item.action) {
        if (item.action === 'logout') {
          this.$buefy.toast.open({
            message: 'Log out clicked',
            type: 'is-danger',
            queue: false
          })
        } else if (item.action === 'dark-mode-toggle') {
          this.$store.commit('darkModeToggle')
        }
      }
    },
    menuSecondaryCloseClick () {
      this.$store.commit('overlayToggle', false)
    },
    menuSecondaryClose () {
      this.menuSecondary = this.menuSecondaryLabel = this.menuSecondaryIcon = null
      this.$store.commit('asideActiveForcedKeyToggle', null)
    }
  },
  watch: {
    isOverlayVisible (newValue) {
      if (!newValue) {
        this.menuSecondaryClose()
      }
    }
  }
}
