import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    // Document title tag
    // We combine it with defaultDocumentTitle set in `src/main.js` on router.afterEach hook
    meta: {
      title: 'Dashboard'
    },
    path: '/',
    name: 'home',
    component: Home
  },
  {
    meta: {
      title: 'Alcance'
    },
    path: '/production',
    name: 'production',
    component: () =>
      import(/* webpackChunkName: "tables" */ '../views/Production.vue')
  },
  {
    meta: {
      title: 'Alcance'
    },
    path: '/mecanics/:id',
    name: 'mecanic',
    component: () =>
      import(/* webpackChunkName: "tables" */ '../views/Mecanic.vue')
  },
  {
    meta: {
      title: 'Charts'
    },
    path: '/charts',
    name: 'charts',
    component: () =>
      import(/* webpackChunkName: "tables" */ '../views/Charts.vue')
  },
  {
    meta: {
      title: 'Charts'
    },
    path: '/charts/3',
    name: 'charts',
    component: () =>
      import(/* webpackChunkName: "tables" */ '../views/Charts2.vue')
  },
  {
    meta: {
      title: 'Charts detail'
    },
    path: '/charts/1',
    name: 'charts.edit',
    component: () =>
      import(/* webpackChunkName: "tables" */ '../views/ChartDetail.vue')
  },
  {
    meta: {
      title: 'Charts detail'
    },
    path: '/charts/2',
    name: 'charts.edit',
    component: () =>
      import(/* webpackChunkName: "tables" */ '../views/ChartDetail2.vue')
  },
  {
    meta: {
      title: 'Log Curves'
    },
    path: '/log-curves',
    name: 'log-curves',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "log-curves" */ '../views/Tables.vue')
  },
  {
    meta: {
      title: 'Nuevo pozo'
    },
    path: '/forms',
    name: 'forms',
    component: () =>
      import(/* webpackChunkName: "forms" */ '../views/Forms.vue')
  },
  {
    meta: {
      title: 'Mi Cuenta'
    },
    path: '/profile',
    name: 'profile',
    component: () =>
      import(/* webpackChunkName: "profile" */ '../views/Profile.vue')
  },
  {
    meta: {
      title: 'New Client'
    },
    path: '/client/new',
    name: 'client.new',
    component: () =>
      import(/* webpackChunkName: "client-form" */ '../views/ClientForm.vue')
  },
  {
    meta: {
      title: 'Edit Client'
    },
    path: '/client/:id',
    name: 'client.edit',
    component: () =>
      import(/* webpackChunkName: "client-form" */ '../views/ClientForm.vue'),
    props: true
  },
  {
    meta: {
      title: 'Parámetros Geomecánicos'
    },
    path: '/parametros-mecanicos',
    name: '/parametros-mecanicos',
    component: () =>
      import(
        /* webpackChunkName: "parametros-mecanicos" */ '../views/GeomecanicParams.vue'
      ),
    props: true
  },
  {
    meta: {
      title: 'Propiedades Mécanicas'
    },
    path: '/propiedades-mecanicas',
    name: '/propiedades-mecanicas',
    component: () =>
      import(
        /* webpackChunkName: "propiedades-mecanicas" */ '../views/MecanicProps.vue'
      )
  },
  {
    meta: {
      title: 'Análisis de Estabilidad'
    },
    path: '/estabilidad',
    name: '/estabilidad',
    component: () =>
      import(
        /* webpackChunkName: "estabilidad" */ '../views/EstabilityAnalisys.vue'
      )
  },
  {
    meta: {
      title: 'Geopresiones Finales'
    },
    path: '/geopresiones-finales',
    name: '/geopresiones-finales',
    component: () =>
      import(
        /* webpackChunkName: "geopresiones-finales" */ '../views/FinalGeopresion.vue'
      )
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

export default router
